/* eslint-disable @typescript-eslint/no-unused-vars */
import { useVideo } from '@facephi/sdk-video-recording';
import { Component, Element, Event, EventEmitter, Host, Method, State, Watch, h } from '@stencil/core';
import { LicenseComponents } from '../../state/model';
import { onChange } from '../../state/store';
@Component({
  tag: 'facephi-video-provider',
  styleUrl: 'video-provider.css',
  shadow: true,
})
export class VideoProvider {
  @Element() host: HTMLElement;

  @State() loading = true;
  @State() tenantId: string;
  @State() operationId: string;

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  @Event() changeChannel: EventEmitter<any>;
  @Event() changeLoading: EventEmitter<boolean>;
  @State() videoOptions: { baseUrl: string; apiKey: string; tenantId?: string; operationId?: string };

  private startVideo: () => Promise<void>;
  private handleStopVideo: () => void;
  private handleCameraRear: () => Promise<void>;
  private handleCameraFront: () => Promise<void>;
  private channel;

  @State() component;

  disconnectComponent;

  @Watch('component')
  updateComponent(newValue) {
    if (newValue) {
      this.videoOptions = { ...this.videoOptions, apiKey: newValue.parameters.apiKey, baseUrl: newValue.parameters.baseUrl };
    }
  }

  @Watch('tenantId')
  updateTenantId(newValue) {
    if (newValue) {
      this.videoOptions = { ...this.videoOptions, tenantId: newValue };
    }
  }

  @Watch('operationId')
  updateOperationId(newValue) {
    if (newValue) {
      this.videoOptions = { ...this.videoOptions, operationId: newValue };
    }
  }

  @Watch('videoOptions')
  checkVideoOptions(newValue) {
    if (newValue.apiKey && newValue.baseUrl && newValue.tenantId && newValue.operationId) {
      this.handleVideo(newValue.operationId, newValue.tenantId);
      this.startVideo();
      this.loading = false;
    }
  }

  connectedCallback() {
    this.disconnectComponent = onChange('components', components => {
      this.component = components[LicenseComponents.videoRecording];
      this.tenantId = components[LicenseComponents.tracking]?.parameters?.tenantId;
    });

    this.disconnectComponent = onChange('tenantId', tenantId => {
      this.tenantId = tenantId;
    });

    this.disconnectComponent = onChange('operationId', operationId => {
      this.operationId = operationId;
    });
  }

  handleChannel = channel => {
    this.channel = channel;
    this.changeChannel.emit(this.channel);
  };

  private handleVideo(operationId: string, tenantId?: string) {
    if (this.videoOptions.baseUrl && this.videoOptions.apiKey) {
      const { setParams, handleStart, handleStop, setCameraFront, setCameraRear } = useVideo({
        baseUrl: this.videoOptions.baseUrl,
        apiKey: this.videoOptions.apiKey,
        audio: true,
        setChannel: this.handleChannel,
      });
      this.startVideo = handleStart;
      this.handleStopVideo = handleStop;
      this.handleCameraRear = setCameraRear;
      this.handleCameraFront = setCameraFront;
      setParams({ operationId, tenantId });
    }
  }

  @Watch('loading')
  handleLoading() {
    this.changeLoading.emit(this.loading);
  }

  disconnectedCallback() {
    this.handleStopVideo();
    this.disconnectComponent();
  }

  @Method()
  async stopVideo() {
    this.handleStopVideo?.();
  }

  @Method()
  async setCameraRear() {
    return this.handleCameraRear?.();
  }

  @Method()
  async setCameraFront() {
    return this.handleCameraFront?.();
  }

  @Method()
  async getChannel() {
    return this.channel;
  }

  render() {
    return this.loading ? null : (
      <Host>
        <slot />
      </Host>
    );
  }
}
