import AgoraRTC, {
  ICameraVideoTrack,
  IMicrophoneAudioTrack
} from 'agora-rtc-sdk-ng';
import { Credentials, useRecordingWs } from './useRecordingWs';

export type VideoRecordingProps = {
  baseUrl: string;
  apiKey: string;
  audio?: boolean;
  video?: boolean;
  handleError?(hasError: boolean): void;
  handleRecording?(isRecording: boolean): void;
  setChannel?({
    audio,
    video,
  }?: {
    audio: IMicrophoneAudioTrack | null;
    video: ICameraVideoTrack | null;
  }): void;
};

AgoraRTC.setLogLevel(4 /* NONE */);
type Tracks = [
  IMicrophoneAudioTrack | undefined,
  ICameraVideoTrack | undefined
];

type FacingMode = 'user' | 'environment';

export const useVideo = ({
  baseUrl,
  apiKey,
  audio,
  video = true,
  setChannel,
  handleRecording,
  handleError,
}: VideoRecordingProps) => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  let tracksRef: Tracks = [] as any;
  let error: boolean = false;
  let errorWs: boolean = false;
  const agoraClient = AgoraRTC.createClient({
    mode: 'rtc',
    codec: 'vp8',
  });
  let hasAudio: boolean = audio;
  let hasVideo: boolean = video;

  const setAudio = (enabledAudio: boolean) => {
    hasAudio = enabledAudio;
  }

  const setVideo = (enabledVideo: boolean) => {
    hasVideo = enabledVideo;
  }

  const handleVideoError = () => {
    handleError?.(error || errorWs);
  }

  const setError = (hasError: boolean) => {
    error = hasError;
    handleVideoError();
  }

  const handleCredentials = (credentials: Credentials | undefined) => {
    if (credentials) {
      joinToChannel(credentials);
    }
  };

  const handleErrorWs = (hasError: boolean) => {
    errorWs = hasError;

    if (hasError) {
      closeTracks();
    }
    handleVideoError();
  }

  const {
    setParams: setParamsWs,
    getCredentials,
    startRecord,
    stopRecord,
  } = useRecordingWs({
    apiUrl: `wss://${baseUrl}/identification`,
    apiKey,
    handleRecording,
    handleCredentials,
    handleError: handleErrorWs,
  });

  const setParams = ({autoStart, tenantId, operationId}: {autoStart?: boolean, tenantId?: string, operationId?: string}) => {
    handleStop();

    if (operationId) {
      setParamsWs({operation: operationId, tenant: tenantId});
      if (autoStart) {
        handleStart();
      }
    }
  };

  const handleMuted = () => {
    if (!hasAudio) {
      tracksRef[0]?.setMuted(true);
    }
    if (!hasVideo) {
      tracksRef[1]?.setMuted(true);
    }
  };

  const onChangeChannel = () => {
    const channel = {
      audio: tracksRef[0] || null,
      video: tracksRef[1] || null,
    };
    const hasTrack = Object.values(channel).some((item) => item);
    setChannel?.(hasTrack ? channel : undefined);
  };

  const handleStart = async () => {
    setError(false);
    try {
      const [audioTrack, videoTrack] =
        await AgoraRTC.createMicrophoneAndCameraTracks(undefined, {
          encoderConfig: '720p_2',
          facingMode: 'environment',
        });

      tracksRef = [audioTrack, videoTrack];
      handleMuted();
      onChangeChannel();
      getCredentials();
    } catch {
      setError(true);
    }
  };

  const joinToChannel = async (credentials: Credentials) => {
    try {
      await agoraClient.join(
        credentials.appId,
        credentials.channel,
        credentials.cameraToken,
        credentials.cameraUid
      );

      tracksRef?.forEach(async (item) => {
        if (item) {
          await agoraClient.publish(item);
        }
      });
      startRecord();
    } catch {
      setError(true);
    }
  };

  const closeTracks = () => {
    tracksRef?.forEach(async (item) => {
      item?.stop();
      item?.close();
    });
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    tracksRef = [] as any;
    onChangeChannel();
    if (agoraClient.connectionState === 'CONNECTED') {
      agoraClient.unpublish();
      agoraClient.leave();
    }
  };

  const handleStop = () => {
    stopRecord();
    closeTracks();
  };

  const getCameras = async () => {
    const cameras = await AgoraRTC.getCameras();
    return cameras.map(({ deviceId, label }) => ({
      deviceId,
      label,
    }));
  };

  const getTypeCameras = async (facingMode: FacingMode) => {
    const cameras = await getCameras();
    return cameras.reduce(async (acc, item) => {
      const rest = await acc;
      try {
        const camera = await navigator.mediaDevices.getUserMedia({
          video: {
            facingMode: { exact: facingMode },
            deviceId: { exact: item.deviceId },
          },
        });
        if (camera) {
          rest.push(item.deviceId);
        }
      } catch {
        /* Empty block */
      }
      return rest;
    }, Promise.resolve([] as string[]));
  };


  const setCamera = async (facingMode: FacingMode) => {
    const availableCameras = await getTypeCameras(facingMode);
    const currentDeviceId = tracksRef[1]?.getMediaStreamTrack()?.getSettings()?.deviceId;
    if (availableCameras?.length && currentDeviceId !== availableCameras[0]) {
      await tracksRef[1]?.setDevice(availableCameras[0]);
      handleMuted();
      onChangeChannel();
    }
  };

  return {
    setAudio,
    setVideo,
    setParams,
    handleStop,
    getCameras,
    getTypeCameras,
    handleStart,
    setCameraRear: () => setCamera('environment'),
    setCameraFront: () => setCamera('user'),
  };
};
