enum WsMessage {
  CREDENTIALS_REQUEST = 'CREDENTIALS_REQUEST',
  CREDENTIALS_RESPONSE = 'CREDENTIALS_RESPONSE',
  WAITING_CONFIRMATION = 'WAITING_CONFIRMATION',
  IDENTIFICATION_STARTED = 'IDENTIFICATION_STARTED',
  STOP = 'STOP',
  IDENTIFICATION_STOPPED = 'IDENTIFICATION_STOPPED',
}

export type Credentials = {
  appId: string;
  channel: string;
  microphoneUid: string;
  microphoneToken: string;
  screenUid: string;
  screenToken: string;
  cameraUid: string;
  cameraToken: string;
};

type IProps = {
  apiUrl: string,
  apiKey: string;
  handleRecording?(value: boolean): void;
  handleCredentials(credentials: Credentials | undefined): void;
  handleError(hasError: boolean): void;
};

export const useRecordingWs = ({ apiUrl, apiKey, handleRecording, handleCredentials, handleError }: IProps) => {
  let websocket: WebSocket | null;
  let error: boolean = false;
  let enabled: boolean = false;
  let idTimeout: NodeJS.Timeout | null;
  let operationId: string;
  let tenantId: string;

  const setParams = ({operation, tenant}: {operation: string, tenant?: string}) => {
    tenantId = tenant;
    if (operationId !== operation) {
      operationId = operation;
      setWebsocket();
    }
  }

  const setWebsocket = () => {
    if (
      websocket &&
      websocket.readyState === websocket.OPEN
    ) {
      closeWebSocket(false);
    }
    websocket = null;

    if (enabled && operationId) {
      const ws = new WebSocket(apiUrl);
      ws.onerror = () => setError(true);
      ws.onmessage = (lastMessage) => handleMessage(lastMessage.data);
      ws.onopen = () => sendCredentials();
      websocket = ws;
    }
  }

  const setError = (value: boolean) => {
    error = value;
    if (error) {
      closeWebSocket();
    }
    handleError(value);
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const sendWs = (data: any) => {
    if (
      websocket &&
      websocket.readyState === websocket.OPEN
    ) {
      websocket.send(JSON.stringify(data));
    }
  };

  const handleMessage = (data: string) => {
    if (data) {
      const message = JSON.parse(data);
      switch (message.type) {
        case WsMessage.CREDENTIALS_RESPONSE:
          handleCredentials(message.credentials);
          break;
        case WsMessage.IDENTIFICATION_STARTED:
          handleLoading(false);
          handleRecording?.(true);
          break;
      }
    }
  };

  const sendCredentials = () => {
    sendWs({
      type: WsMessage.CREDENTIALS_REQUEST,
      apiKey: apiKey,
      sessionId: operationId,
    });
  };

  const handleLoading = (isLoading: boolean) => {
    idTimeout && clearTimeout(idTimeout);
    idTimeout = null;
    if (isLoading) {
      idTimeout = setTimeout(() => {
        setError(true);
      }, 15000);
    }
  };

  const setEnabled = (value: boolean) => {
    if (enabled !== value) {
      enabled = value;
      setWebsocket();
    }
  }

  const getCredentials = () => {
    setEnabled(true);
    handleLoading(true);
  };

  const startRecord = () => {
    sendWs({
      type: WsMessage.WAITING_CONFIRMATION,
      sessionId: operationId,
      tenantId,
    });
  };

  const stopRecord = () => {
    sendWs({
      type: WsMessage.STOP,
      apiKey: apiKey,
      sessionId: operationId,
      tenantId,
    });
  };

  const resetVariables = () => {
    setError(false);
    handleRecording?.(false);
    handleLoading(false);
    handleCredentials(undefined);
  };

  const closeWebSocket = (enabled = true) => {
    if (websocket) {
      stopRecord();
      websocket.close();
    }
    resetVariables();
    if (enabled) {
      setEnabled(false);
    }
  };

  return {
    setParams,
    getCredentials,
    startRecord,
    stopRecord: closeWebSocket,
  }
}

